export default {
	data() {
		return {
			isDad: true
		}
	},
	
	methods: {
		// 跳转页面
		jumpPage(num){
			if(num == 1) {
				uni.navigateTo({
					url:'../../pages/Web/index'
				})
			}else {
				uni.navigateTo({
					url:'../../pages/User/aboutUs'
				})
			}
		},
		
		// 身份切换
		switchDad(){
			uni.showModal({
			    title: '提示',
			    content: '确定要切换爸爸/妈妈的身份?',
				success: (res) =>{
					if(res.confirm){
						  this.isDad= !this.isDad
					}else if (res.cancel) {
						console.log('用户取消了')
					}
				}
			});
		}
	}
}
